import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';

/*
  Generated class for the PageConfigProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class PageConfigProvider {

  constructor(public http: HttpClient) {
    console.log('Hello PageConfigProvider Provider');
  }
  /**
   * login登陆页图片配置信息
   */
  public static LoginPagePicture="/assets/imgs/me/login/login-logotitle.png"
  /**
   * home页面轮播图配置信息
   */
  public static HomePageTogglePicture=[{ "picurl": "assets/imgs/home/slider01.jpg" }, { "picurl": "assets/imgs/home/slider02.jpg" }, { "picurl": "assets/imgs/home/slider03.jpg" }]
}



