import { Component } from '@angular/core';
// import { TitleCasePipe } from '@angular/common';
import { OnInit, Input, Output, EventEmitter } from '@angular/core';
import { NavController, ToastController, AlertController,ModalController } from 'ionic-angular';
import { HttpServicesProvider } from '../../providers/http-services';

/**
 * Generated class for the ToggleModelComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'toggle-model',
  templateUrl: 'toggle-model.html'
})
export class ToggleModelComponent implements OnInit {
  @Input() msg: any;
  @Input() defaultvalue: boolean;
  @Input() key: string;
  @Input() test:string;
  //子组件向父组件传值；
  @Output() cancle = new EventEmitter;
  ngOnInit() {
    if (this.msg.a == '正常') {
      this.defaultvalue == null || this.defaultvalue == false ? this.ischeckbox = false : this.ischeckbox = true;
    } else {
      this.defaultvalue == false ? this.ischeckbox = false : this.ischeckbox = true;
    }
  }
  public ischeckbox;
  constructor(public navCtrl: NavController,
    public httpService: HttpServicesProvider,
    public toastCrtl: ToastController,
    public alertCtrl: AlertController,
    public modalCtrl:ModalController) {
  }
  check(): void {
    console.log(this.test);
    if (this.ischeckbox == true && this.msg.a == '正常') {
      let alertctrl = this.alertCtrl.create({
        title: '确认当前异常信息',
        cssClass:'AlertList',
        buttons: [{
          text: '异常登记',
          
          handler: () => {
            this.ischeckbox = !this.ischeckbox;
            this.cancle.emit('确认');
          }
        }, {
          text: '返回',
          handler: () => {
            this.cancle.emit('取消');
          }
        }]
      })
      alertctrl.present();
      
    }

    else{
      this.ischeckbox=!this.ischeckbox;
    }
    
  }

}



