import { Component, Input, Output, EventEmitter } from '@angular/core';
import { HttpServicesProvider } from '../../providers/http-services';

/**
 * Generated class for the ExcelToTableComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'excel-to-table',
  templateUrl: 'excel-to-table.html'
})
export class ExcelToTableComponent {
  @Input() jsondata; // excel json data
  text: string;

  constructor() {
    this.text = 'Hello World';
  }

  ngOnInit() {
    // console.log(this.jsondata);

    this.readerTable(this.jsondata)
  }


  readerTable(json) {
    const excelObj = JSON.parse(json)
    const tableData = excelObj['data']
    const tRows = excelObj['row']
    const tCols = excelObj['column']
    const rowHeight = excelObj['defaultRowHeight']
    const colWidth = excelObj['defaultColWidth']
    const celldata = tableData[0]['celldata']

    console.log(celldata);
    

  }
}



