    WorkflowServiceProvider,
    DatePicker,
    PhoneDeviceProvider,
    BackButtonProvider,
    // DocumentViewer,
    RoundProgressModule,
    DragulaService,
    Calendar,
    AppAvailability,
    PageConfigProvider,
    SQLite,
    SqliteProvider,
    BLE,
    NFC, Ndef
