import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'page-head',
  templateUrl: 'page-head.html'
})
export class PageHeadComponent implements OnInit{
  text: string;
  @Input() title:string;
  constructor() {
    console.log(this.title);
  }
  ngOnInit(){
    console.log(this.title);
  }
  ngOnChanges(){
    console.log(this.title);
  }
}



