import { Component, Input, OnInit } from '@angular/core';
import * as $ from 'jquery';
import { NavController } from 'ionic-angular';
/**
 * Generated class for the RingslideComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'ringslide',
  templateUrl: 'ringslide.html'
})
export class RingslideComponent implements OnInit {
  @Input() slideList;
  @Input() slideTitle: string;
  @Input() slideType: string;
  public isshow:boolean=false;
  public isshow2:boolean=false;
  text: string;
  public timer;
  constructor(
    public navCtrl:NavController,
  ) {

  }
  ngOnInit() {
    // this.slidePlay();
    if(this.slideType=='piRisk'){
      this.isshow=true;
    }else if(this.slideType=='imDefect'){
      this.isshow2=true;
    }
  }
  ngOnChanges() {
    setTimeout(() => {
      if (this.slideList && this.slideList.length > 4) {
        // this.slideList.push(this.slideList[0]);
        this.slidePlay();
      }
    }, 0);
  }
  ngAfterViewChecked() {
    //  console.log(this.slideList);
    // setTimeout(this.slidePlay,300);
    // console.log(new Date())
  }
  ngOnDestroy() {
    if(this.timer){
      clearInterval(this.timer);
    }
  }
  slidePlay() {
    let index = 0;
    let startPosition = 0;
    let singleHeight = 35;
    let endPostion: number;
    if(!this.timer){
      this.timer = setInterval(() => {
      index++;
      endPostion = startPosition - index * singleHeight;
      $('.actual-slidelist').animate({'margin-top': endPostion + 'px'},500,()=>{
         if (index == this.slideList.length - 4) {
        index = 0;
        endPostion = 0;
        $('.actual-slidelist').css('margin-top',startPosition + 'px');
      }
      });
     
    }, 3000);
    }
    
  }
  skiptoNewpage(item){
    let type=item['type'];
    if(type=='imDefect'){
      this.navCtrl.push('ImdefectinfoPage',{'inDefect':item['id']});
    }else if(type=='leAbnormal'){
      this.navCtrl.push('AbnormalinfoPage',{'abnormalInfo':item})
    }
  }
  onclickimDefect(item){
    this.navCtrl.push('ImdefectinfoPage',{'inDefect':item['defectid'],'readControll':false});
  }
  showScroll(){
    clearInterval(this.timer);
    // this.slideList.splice(0,4);
    $('.slide-content').css('overflow','scroll');
  }
}



