
import { Injectable } from '@angular/core';
import { Camera, CameraOptions } from '@ionic-native/camera';
// import { BarcodeScanner } from '@ionic-native/barcode-scanner';
import { Network } from '@ionic-native/network';
import { PhotoLibrary } from '@ionic-native/photo-library';
import { Media } from '@ionic-native/media';
import { File } from '@ionic-native/file';
import { MediaCapture } from '@ionic-native/media-capture';
import { StreamingMedia } from '@ionic-native/streaming-media';
import { PhotoViewer } from '@ionic-native/photo-viewer'
import { VideoEditor } from '@ionic-native/video-editor';
import { ThemeableBrowser, ThemeableBrowserObject } from '@ionic-native/themeable-browser';
import { ConfigProvider } from '../providers/config';
import { QRScanner } from '@ionic-native/qr-scanner';
import { GalleryModal } from 'ionic-gallery-modal';
import { ModalController, Platform } from 'ionic-angular';
import { FileTransfer } from '@ionic-native/file-transfer';
// import { ScannerPage } from '../pages/scanner/scanner';
@Injectable()
export class PhoneDeviceProvider {
  public imagedata;
  constructor(
              public camera:Camera,
              public network:Network,
              public photolibrary:PhotoLibrary,
              public media:Media,
              public file:File,
              public mediaCapture:MediaCapture,
              public streamingMedia:StreamingMedia,
              public photoviewer:PhotoViewer,
              public videoeditor:VideoEditor,
              // public barcodeScanner:BarcodeScanner,
              // public qrscanner:QRScanner,
              public brower:ThemeableBrowser,
              public modalCtrl:ModalController,
              public fileTransfer:FileTransfer,
              public platform:Platform
              ) {
    
  }
  /**
   * 手机扫码功能
   */
 public doScan(navctrl) {
  navctrl.push('ScannerPage',null,{"animate":false,"type":'leasset'});
  }
  /**
   * 手机拍照
   */
  doPicture(){
    this.doCamera(this.camera.PictureSourceType.CAMERA);
    return this.imagedata;
  }
  /**
   * 从相册中获取图片
   */
  doPhotolibray(){
    this.doCamera(this.camera.PictureSourceType.PHOTOLIBRARY);
    return this.imagedata;
  }
  /**
   * 
   * @param sourceType 
   */
  doCamera(sourceType){
    const options: CameraOptions = {
      quality: 100, //图片质量
      destinationType: this.camera.DestinationType.FILE_URI,  /*返回的类型*/
      encodingType: this.camera.EncodingType.JPEG,
      sourceType: sourceType,
      correctOrientation: true,//陀螺仪纠正照片方向
      // mediaType: this.camera.MediaType.PICTURE,
      //allowEdit:true,
      targetWidth: 1024,  /*宽度高度要设置*/
      targetHeight: 1024
    }
    let that=this;
    this.camera.getPicture(options).then((imageData) => {
      console.log(imageData);
      that.imagedata=imageData;
      this.photolibrary.saveImage(imageData, '天开云');
  },(err)=>{
    console.log('camera error');
  });
}
/**
 * 显示大图
 * @param url 图片地址数组
 * @param index 第一张图片的位置
 */
 public showBigimage(itemList,index=0){
  let photos = [];
    // photos.push({url:itemList});
    console.log(itemList+"\n"+index);
    if(Array.isArray(itemList)==true){
      for (let i = 0; i < itemList.length; i++) {
        photos.push({ url: itemList[i]['url'] });
      }
    }else{
      photos.push({url:itemList});
    }
    
    // photos.push({url:item});

    // let src='file:///android_asset/www/'+document.getElementsByClassName('pitaskitems-image')[0].getAttribute('src');
    // let api = 'http://192.168.1.21:8100/assets/imgs/application/pitaskinfoPage/assetTest.png';
    // this.photoView.show(item,'设备指引图片',{share:true});
    // this.photoView.show('https://img.zcool.cn/community/0161eb554909920000019ae934d5c2.jpg@1280w_1l_2o_100sh.jpg');
    let modal = this.modalCtrl.create(GalleryModal, { photos: photos, initialSlide: index, closeIcon: 'ios-close-outline' });
    modal.present();

}
  /**
   * @param bussid id
   * @param busstype 类型
   * @param options 参数
   * @param data 
   */
  openInAppBrower(bussid,busstype,options,data){
    let url ='';
    var exceptHead = ''
    for (let i = 1; i < busstype.length; i++) {
      exceptHead += busstype[i];
    }
    let bussname = busstype[0].toLocaleLowerCase() + exceptHead;
    if(bussid==undefined || bussid==''){
      url = ConfigProvider.apiUrl +'/'+ bussname + '/edit/' + data[Object.keys(data)[0]];
    }else{
      url = ConfigProvider.apiUrl +'/'+ bussname + '/edit/' + bussid;
    }
    console.log(url);
    const browser: ThemeableBrowserObject = this.brower.create(url, '_blank', options);
  }
  /**
   * @param url
   * @param filename
   * 
   * @param callback 回调函数
   */
  filedownLoad(url,filename,callback){
    let filetransfer =this.fileTransfer.create();
    //本地文件路径
    let filepath=this.file.externalCacheDirectory+filename;
    filetransfer.download(url,this.file.externalCacheDirectory+filename).then(()=>{
      callback(filepath);
    })
  }
  /**
   * 硬件的物理返回
   * 
   */
  phoneBacktoRoot(navctrl){
    this.platform.registerBackButtonAction(()=>{
      navctrl.popToRoot();
    })
  }
  phoneBacktoPop(navctrl){
    this.platform.registerBackButtonAction(()=>{
      navctrl.pop();
    })
  }
}



