import { Component, Input, OnInit } from '@angular/core';
import { HttpServicesProvider } from '../../providers/http-services';
declare var $: any
/**
 * Generated class for the LmsummaryListComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'lmsummary-list',
  templateUrl: 'lmsummary-list.html'
})
export class LmsummaryListComponent implements OnInit {
  @Input() code:'';
  public list = []; // 列表
  public projectid = '';

  constructor(
    public httpService:HttpServicesProvider,
  ) {}

  // ionViewDidLoad() {
  //   console.log('ionViewDidLoad LmtagsummaryPage');
  //   this.initData()
  // }
  ngOnInit() {//当将要进入页面时触发
    this.list = [];
    this.initproject();
  }
  initproject() {
    this.httpService.get('/query/lmpj/lmpjProject/headlist?format=list', null, (result) => {
      this.projectid = result[0]['projectid'];
      this.initData();
    });
  }
  initData() {
    let url = `/${this.code}/list?sort=summaryid&order=desc&_search=false&rows=15&page=1&sidx=&sord=asc&projectid=${this.projectid}`
    this.httpService.get(url, null, (result) => {
      if (result != null) {
        var rows = result.rows;
        this.list = rows;
      }
    });
  }

}



