import { Component ,OnInit, Input,Output, EventEmitter} from '@angular/core';
import { listLazyRoutes } from '@angular/compiler/src/aot/lazy_routes';
import * as $ from 'jquery';
/**
 * Generated class for the StarscoreComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'starscore',
  templateUrl: 'starscore.html'
})
export class StarscoreComponent implements OnInit{
  @Input() value: any; 
  @Input() len: number; 
  @Input() ishidden:boolean;
  @Input() readtype:string;
  @Output() backvalue=new EventEmitter();
  public list=[];
  ngOnInit(){
    for(let i=0;i<5;i++){
      this.list.push({
        index:i,
        isshow:false,
        isleft:false,
      })
    }
    if(this.value=='' || this.value==undefined || this.value==null){
      for(let i = 0;i<5;i++){
        this.list[i].isshow=false;
        this.list[i].isleft=false;
      }
    }else{
      for(let i =0;i<this.value;i++){
        this.list[i].isshow=true;
        if(this.value!=parseInt(this.value)){
          this.list[parseInt(this.value)].isleft=true;
        }
      }
    }
  }
  constructor() {

    console.log('this is in constructor'+this.value);
   
  }
   //星星评分
  //  chooseOne(item,event){

  //   // console.log(event.clientX);
  //   // let positionX=event.clientX;
  //   // let elementX=document.getElementsByClassName('rating')[item.index].getBoundingClientRect().left;
  //   // if(starlen !=2 ){
  //   //   item.isleft=false
  //   //   this.backvalue.emit(item.index+1);
  //   // }else{
  //   //   item.isleft=true;
  //   //   this.backvalue.emit(item.index+0.5);
  //   // }
  // }
  chooseOne(item,event){
    for(let i = 0;i<5;i++){
      this.list[i].isshow=false;
      this.list[i].isleft=false;
    }
    for(let i=0;i<=item.index;i++){
      this.list[i].isshow=true;
    }
    if(this.len == 2){
      console.log(2)
      item.isleft=false
      this.backvalue.emit(item.index+1);
    }else{
      let positionX=event.clientX;
      let elementX=document.getElementsByClassName('rating')[item.index].getBoundingClientRect().left;
      if(positionX-elementX>25){
        item.isleft=false
        this.backvalue.emit(item.index+1);
      }else{
        item.isleft=true;
        this.backvalue.emit(item.index+0.5);
      }
    }
  }

}



