  ngOnInit() {//在 Angular 第一次显示数据绑定和设置指令/组件的输入属性之后，初始化指令/组件
    
    // console.log('--mform ngOnInit--', this.formdata);
    
    this.formobjval[this.code]=this.formVal;
    this.formobj[this.code]=this.formdata;
    var formdata = this.formdata;
    for(var i = 0;i<formdata.length;i++){
      if(formdata[i].type == 'select'){
        if(formdata[i].dicttype =='selectDiy'){
          this.selectArray[formdata[i].index] = formdata[i].array;
        }else{
        this.selectArray[formdata[i].index] =[];
        this.getDict(formdata[i].index,formdata[i].dicttype);//select
        }
        // this.selectArray.push()
      }else if(formdata[i].type == 'radiogroup'){
        this.radioArray[formdata[i].index] =[];
        this.radiogetDict(formdata[i].index,formdata[i].dicttype);//radio
        // this.starscorelist(formdata[i].index,formdata[i].starlen);
      }else if(formdata[i].type == 'starscore'){
        this.starArray[formdata[i].index] =[];
        this.starscorelist(formdata[i].index,formdata[i].starlen);
      }else if(formdata[i].type == 'uploadify'){
        this.imagesArray[formdata[i].index] =[];
        this.initPicture(formdata[i].busstype,this.id,formdata[i].index);

      }else if(formdata[i].type == 'datetime'){
        this.foucsarray[formdata[i].index]=true;
        this.timerarray[formdata[i].index]='';
        this.dateArray.push({'index':formdata[i].index,'format':formdata[i].format})

      }
    }
    this.formdata = formdata;

    // 监听物理返回按钮事件
    this.platform.registerBackButtonAction(() => {
      // 关闭datetime-picker
      var pickerClearButton = document.getElementsByClassName("picker-button")[0];
      if (pickerClearButton != undefined) {
        var clickEvent = new MouseEvent("click", {
          "view": window,
          "bubbles": true,
          "cancelable": false
        });
        pickerClearButton.dispatchEvent(clickEvent);
      }
      this.navCtrl.pop();
    })
  }


  ngOnChanges(){
    var that =this;
    var formdata = this.formdata;
    for(var i = 0;i<formdata.length;i++){
      if(formdata[i].type == 'select'){
        if(formdata[i].dicttype =='selectDiy'){
          this.selectArray[formdata[i].index] = formdata[i].array;
        }else{
        this.selectArray[formdata[i].index] =[];

        this.getDict(formdata[i].index,formdata[i].dicttype);//select

        }
        // this.selectArray.push()
      }else if(formdata[i].type == 'datetime'){
        if(this.formVal[formdata[i].index] == undefined || this.formVal[formdata[i].index] == ""){
          this.formVal[formdata[i].index+formdata[i].index] = new Date(new Date(Utils.dateFormat(new Date(),formdata[i].format)).getTime()+8*60*60*1000).toISOString();
        }


      }
      if(formdata[i].starttoggle){
        if($('#form_'+this.code+' #folder_'+i).length == 1 && $('#form_'+this.code+' #startcollapse'+formdata[i].toggleindex).length == 0){
         $('#form_'+this.code+' #folder_'+i).before('<div  class="folder-container foldertoggle" id="startcollapse'+formdata[i].toggleindex+'" data-index="'+i+'" data-toggleindex="'+formdata[i].toggleindex+'"><div class="folder-header"><div class="folder-header__title myclass">'+formdata[i].plantitle+'</div><div class="folder-header__icon"><div class="collapse_hide_icon'+i+'" style="display: none;"><ion-icon name="ios-arrow-down-outline" role="img" class="icon icon-md ion-ios-arrow-down-outline" aria-label="arrow down-outline" ng-reflect-name="ios-arrow-down-outline"></ion-icon></div><div class="collapse_show_icon'+i+'"><ion-icon name="ios-arrow-up-outline" role="img" class="icon icon-md ion-ios-arrow-up-outline" aria-label="arrow up-outline" ng-reflect-name="ios-arrow-up-outline"></ion-icon></div></div> </div></div>')
        }
      }
      if(formdata[i].endtoggle){
        if($('#form_'+this.code+' #folder_'+i).length == 1 && $('#form_'+this.code+' #endcollapse'+formdata[i].index).length == 0){
          $('#form_'+this.code+' #folder_'+i).after('<div class="folder-content" id="endcollapse'+formdata[i].index+'"></div>')
        }
      }
    }
    $('#form_'+this.code+' .foldertoggle').unbind().click(function(){
      let index = $(this).attr('data-index');
      let toggleindex = $(this).attr('data-toggleindex');
      $('#form_'+that.code+' #startcollapse'+toggleindex).nextUntil('#form_'+that.code+' #endcollapse'+toggleindex).toggle();
      
      $('#form_'+that.code+' .collapse_show_icon'+index).toggle();
      $('#form_'+that.code+' .collapse_hide_icon'+index).toggle();
    })
  }
