  getDict(index,dicttype) {//请求数据字典初始化
    var that = this;
    that.httpService.getDict(dicttype, data => {//隐患类型
      if (data !== "") {
        that.selectArray[index] = data;
      }
    })
  }
  radiogetDict(index,dicttype) {//请求数据字典初始化
    var that = this;
    that.httpService.getDict(dicttype, data => {//隐患类型
      if (data !== "") {
        that.radioArray[index] = data;
      }
    })
  }
