    /**
     * 选择签名
     */
    chooseSign(index,item) {
      this.navCtrl.push('SignaturepadPage',{
        typeindex:index,
      });
      this.events.subscribe('signatureImage:signatureImageinfo', (data, time) => {
          this.formVal[data.typeind] = data.img;
          if(item.indextime != undefined){
           this.formVal[item.indextime] = new Date(new Date().getTime()+8*60*60*1000).toISOString();
          }
          this.events.unsubscribe('signatureImage:signatureImageinfo');

      })
    }
    chooseSignMultiple(index) {
      this.navCtrl.push('SignaturepadPage',{
        typeindex:index,
      });
      this.events.subscribe('signatureImage:signatureImageinfo', (data, time) => {
        if( this.formVal[data.typeind]!="" &&  this.formVal[data.typeind]!=undefined){
          this.formVal[data.typeind] = this.formVal[data.typeind] + '|' + data.img;
        }else{
          this.formVal[data.typeind] = this.formVal[data.typeind] + data.img;
        }
        let str8 ='';
        var strArr=this.formVal[data.typeind].split("|");
      // console.log(strArr.length)
          if(strArr.length>1){
            for(let j = 0,len=strArr.length; j < len; j++) {
              str8 = str8+'<img style="width:100px;height:30px;padding:0 2px;"  id="imgworkmembersbase" src="'+strArr[j]+'"/>';
            };
          }else{
            str8 = '<img style="width:100px;height:30px;"  id="imgworkmembersbase"  src="'+this.formVal[data.typeind]+'"/>';
          }
        $('#row_'+data.typeind).html(str8);
          this.events.unsubscribe('signatureImage:signatureImageinfo');

      })
    }
  // 选择项目
  chooseItemname(id, name) {
    this.navCtrl.push('ProjectdialogPage', {});
    this.events.subscribe('project', (item) => {
      this.formVal[name] = item['projectname']
      this.formVal[id] = item['prjid'];
      this.events.unsubscribe('project');
      this.chooseFromChild.emit(item);
    })
  }

    photo(busstype,idx,video) {
      const isBrowser = this.storage.get(StorageProvider.IS_BROWSER);

      if (isBrowser) {
        this.browserPhoto(busstype, idx, video);
        return false;
      }

      const actionsheet = this.actionsheetCtrl.create({
        // cssClass:'headChoice',
        buttons: [
          {
            text: '拍照',
            role: 'destructive',
            cssClass: 'normal',
            handler: () => {
              this.doCamera(this.camera.PictureSourceType.CAMERA,busstype,idx,video);
            }
          }, {
            text: '从手机相册中选择',
            cssClass: 'normal',
            handler: () => {
              this.doCamera(this.camera.PictureSourceType.PHOTOLIBRARY,busstype,idx,video);

            }
          },
           {
            text: '取消',
            role: 'cancel',
            cssClass: 'cancel',
            handler: () => {
            }
          }
        ]
      })
      actionsheet.present();
    }
    doCamera(sourceType,busstype,idx,video) {
    
      let mediatype =0;
      if(video){
        mediatype =1;
      }
      const options: CameraOptions = {
        quality: 100, //图片质量
        destinationType: this.camera.DestinationType.FILE_URI,  /*返回的类型*/
        encodingType: this.camera.EncodingType.JPEG,
        sourceType: sourceType,
        mediaType: mediatype,////可选媒体类型：圖片=0，只允许选择图片將返回指定
        correctOrientation: true,//陀螺仪纠正照片方向
        // mediaType: this.camera.MediaType.PICTURE,
        //allowEdit:true,
        targetWidth: 1024,  /*宽度高度要设置*/
        targetHeight: 1024
      }
      // let that = this;
      let obj={};

      this.camera.getPicture(options).then((imageData) => {
        obj['bussid'] = this.id;
        obj['bussty'] = busstype;
        obj['url'] = imageData;
        //是否上传标记
      obj['flag'] = false;
      obj['isWifiSubmit'] = false;
          console.log(JSON.stringify(imageData))
        
        if(video){
        this.photolibrary.saveImage(imageData, '天开云');

          this.httpService.uploadvideo(busstype, obj['bussid'], imageData, (result) => {
            this.initPicture(busstype,obj['bussid'] ,idx);
          });
        }else{
          this.httpService.uploadify(busstype, obj['bussid'], imageData, (result) => {
            this.initPicture(busstype,obj['bussid'] ,idx);
          });
        }
        
        
        this.files.push(obj);
        // $('.addImage').css('margin-left','10px');
      }

      )
    }
