    browserPhoto(busstype, idx, video) {
      // 创建文件标签并插入body
      const fileInput = document.createElement('input');
      fileInput.type = 'file';
      fileInput.accept = 'image/*';
      if (video) {
        fileInput.accept = 'video/*';
      }
      fileInput.style.display = 'none';
      document.body.appendChild(fileInput);
      // 监听文件选择
      fileInput.addEventListener('change', (event) => {
        const file = event.target['files'][0];
        console.log(`fileInput.addEventListener  file => `, file);

        const url: SafeUrl = this.trusturl.bypassSecurityTrustResourceUrl(URL.createObjectURL(file));

        let obj = {};
        obj['bussid'] = this.id;
        obj['bussty'] = busstype;
        obj['url'] = url;
        //是否上传标记
        obj['flag'] = false;
        obj['isWifiSubmit'] = false;
        console.log(JSON.stringify(url))

        if (video) {
          // this.httpService.uploadvideo(busstype, obj['bussid'], imageData, (result) => {
          //   this.initPicture(busstype, obj['bussid'], idx);
          // });
        } else {
          let formdata = new FormData()
          formdata.append('file', file)
          formdata.append('busstype', busstype)
          formdata.append('folderid', '')
          formdata.append('bussid', this.id)
          this.httpService.postFormData('/uploadify/upload', formdata, res => {
            console.log(res);
            if (res.error == 0) {
              this.toastCtrl.create({
                position: 'top',
                message: '上传成功',
                duration: 1200
              }).present();
              this.initPicture(busstype, obj['bussid'], idx);
            }
            document.body.removeChild(fileInput);
          })
        }
        this.files.push(obj);
      })

      fileInput.click();
    }
