import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import * as $ from 'jquery';
/**
 * Generated class for the NormalSelectComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'normal-select',
  templateUrl: 'normal-select.html'
})
export class NormalSelectComponent implements OnInit {

  @Input() SelectList;
  @Output() backValue=new EventEmitter();
    public selectItem="最近一周";
    public isshow:boolean=false;
    
    constructor() {
     
    }
    ngOnInit(){
    
    }
    ngAfterViewInit(){
      let that=this;
      $('page-newleasset').on('click',function(e){
        if(!$('#normalSelect').is(e.target) && $('#normalSelect').has(e.target).length===0){
          // console.log(222222222222222222);
          that.isshow=false;
        }
      });
    }
    ngOnDestroy(){
      $('page-newleasset').off('click');
    }
    showItemList(){
      this.isshow=!this.isshow;
      if(this.isshow==true){
        
      }
    }
    chooseItem(item,index){
      console.log(item);
      this.selectItem=item;
      this.isshow=false;
      this.backValue.emit(item);
    }
  

}



