import { Component, Input, OnInit, Output,EventEmitter } from '@angular/core';
import { HttpServicesProvider } from '../../providers/http-services';
import { NavController } from 'ionic-angular';
import * as $ from 'jquery';
/**
 * Generated class for the TreeNodeComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'tree-node',
  templateUrl: 'tree-node.html'
})
export class TreeNodeComponent implements OnInit{

  @Input() hasChildNode:boolean;
  @Input() assetinfo={};
  @Input() isShow:boolean=true;
  @Input() Image:string;
  @Input() navCtrl:NavController;
  @Input() ClickPositionId:string='';
  @Output() nodeInfo=new EventEmitter();
  public nodeList=[];
  ngOnInit(){
    // console.log(this.assetinfo);
    if(this.assetinfo['isParent']==1){
      this.hasChildNode=true;
    }else if(this.assetinfo['isParent']){
      this.hasChildNode=false;
    }
  }
  constructor(
    public httpService:HttpServicesProvider,
  ) {
  }
  getNodeList(item){
    if(this.hasChildNode==true){
      let url ='/query/leAsset/leAsset/tree?format=list';
      this.httpService.post(url,{id:item['id']},res=>{
        if(res.length>0){
          this.nodeList=res;
        }
      },this.httpService.CONTENTTYPE_FORMURLENCODE);
      this.isShow=!this.isShow;
    }else{
      this.navCtrl.push('LeassetinfoPage',{'assetinfo':this.assetinfo});
    }
  }
  getNodeInfo(assetinfo){
    if(this.ClickPositionId!=''){
      $('#'+this.ClickPositionId).css({'backgroundColor':'white','color':'black'});
    }
    $('#'+assetinfo['id']).css({'backgroundColor':'#0099ff','color':'white'});
    console.log(assetinfo);
    this.nodeInfo.emit(assetinfo);
  }
  saveInfo(event){
    // console.log(event);
    this.nodeInfo.emit(event);
  }
}



