import { Component, ViewChild } from '@angular/core';
import { Platform, NavController, Events, IonicApp } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { JPush } from '@jiguang-ionic/jpush';
import { ConfigProvider } from '../providers/config';
import { JpushUtilProvider } from '../providers/jpush-util';
import { Device } from '@ionic-native/device';
import { HttpServicesProvider } from '../providers/http-services';
import { StorageProvider } from '../providers/storage';


// import { TabsPage } from '../pages/tabs/tabs';
// import { NavController } from 'ionic-angular';

declare const window:any;
@Component({
  templateUrl: 'app.html'
  // template: '<ion-nav #myNav [root]="rootPage"></ion-nav>'
})
export class MyApp {
  @ViewChild('myNav')navCtrl:NavController
  public rootPage:any;
  ngOninit(){

  }
  public registrationId: string;
  devicePlatform: string;
  sequence: number = 0;
  constructor(
    public platform: Platform,
    public statusBar: StatusBar, 
    public splashScreen: SplashScreen,
    public device: Device,
    public httpservice:HttpServicesProvider,
    public jpush:JPush,
    public jpushUtil:JpushUtilProvider,
    public storage:StorageProvider,
    public events: Events,
    private ionicApp: IonicApp
    ) {
      // document.addEventListener('deviceready',()=>{
      //   // this.statusBar.hide();
      //   console.log(this.statusBar.isVisible);
      //   console.log('deviceready is ready');
      //   if(ConfigProvider.environment !='blueEnvironment'){
      //       //延迟隐藏闪屏 防止有白屏
      //       window.setTimeout(() => {
      //         this.splashScreen.hide();
      //         this.statusBar.show();
      //         this.statusBar.overlaysWebView(false);
      //         this.statusBar.backgroundColorByName('white');
      //         this.statusBar.styleDefault();
      //       }, 500);
      //   }
      //   // statusBar.backgroundColorByHexString("#00FFFFFF");
      
      // },false)
  
    if(ConfigProvider.environment=='formalEnvironment'|| ConfigProvider.environment=='jmtcEnvironment'|| ConfigProvider.environment=='delopmentEnvironment'){
      this.jpush.getRegistrationID().then(regid=>{
        
        console.log(regid);
      })
    }
  this.platform.ready().then(() => {
    
    if(ConfigProvider.environment =='blueEnvironment'){
      statusBar.overlaysWebView(true);
      splashScreen.hide(); //启动页
      this.splashScreen.hide();
      this.statusBar.styleBlackTranslucent();
      this.statusBar.backgroundColorByHexString('rgba(0,0,0,0)');
    }
    // this.statusBar.backgroundColorByName('white');
    let offlinepi = this.storage.get(StorageProvider.OFFLINE_PI);
    if(offlinepi==null){
      this.storage.set(StorageProvider.OFFLINE_PI,1);
    }
    var fingeruser = this.storage.get(StorageProvider.SETTING_FINGER_LOGIN);
    console.log(fingeruser)
    console.log(123333)
    if(fingeruser!=null){
      this.rootPage='LoginPage';
    }else{

      if(this.storage.get(StorageProvider.ME_LOGIN_USERINFO)){
        this.rootPage = 'LmtabsPage';
        // this.rootPage = 'TabsPage';
      }else{
        this.rootPage='LoginPage';
      }
    }   
    if(ConfigProvider.environment=='formalEnvironment'|| ConfigProvider.environment=='jmtcEnvironment'|| ConfigProvider.environment=='delopmentEnvironment' || ConfigProvider.environment=='yskbEnvironment'){
      this.httpservice.Upgrader();
    /**极光推送开启 */
    this.jpush.init();
    //插件初始化
    this.jpush.setDebugMode(true);
   /*消息推送配置**/
    //监听初始化
     let userinfo=this.storage.get(StorageProvider.ME_LOGIN_USERINFO);
  
    this.jpushUtil.initResumeAndPause();
  
    this.jpushUtil.setAlias(userinfo['account']);
    
    console.log('app.component.ts中');
    }

    // 当按下后退按钮时，将消除任何形式的模态，重叠或吐司
    // this.platform.registerBackButtonAction(() => {
    //   console.log('registerBackButtonAction');
    //   let activePortal = this.ionicApp._loadingPortal.getActive() ||
    //     this.ionicApp._modalPortal.getActive() ||
    //     this.ionicApp._toastPortal.getActive() ||
    //     this.ionicApp._overlayPortal.getActive();
    //   if (activePortal) {
    //     activePortal.dismiss();
    //   }
    // });

    // 是否在浏览器中运行
    const isBrowser = this.platform.url().startsWith('http');
    console.log('是否在浏览器中运行？ ', isBrowser);
    this.storage.set(StorageProvider.IS_BROWSER, isBrowser);
  });
  
  this.devicePlatform = this.device.platform;
    
  }
  ionViewDidLoad(){
    window.screen.orientation.lock('portrait');
    var fingeruser = this.storage.get(StorageProvider.SETTING_FINGER_LOGIN);
    console.log(fingeruser)
    console.log(1233334444)
    if(fingeruser!=null){
      this.rootPage='LoginPage';
    }else{

      if(this.storage.get(StorageProvider.ME_LOGIN_USERINFO)){
        this.rootPage ='LmtabsPage';
      }else{
        this.rootPage='LoginPage';
      }
    }   
  }
}




