import { Injectable } from '@angular/core';
// import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

/*
  Generated class for the StorageProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class StorageProvider {

  constructor() {
    console.log('Hello StorageProvider Provider');
  }
  //localStorage

  set(key,value){

    localStorage.setItem(key,JSON.stringify(value));  /*对象转换成字符串*/
  }

  get(key){
    return JSON.parse(localStorage.getItem(key));   /*字符串转换成对象*/
  }

  remove(key){
    localStorage.removeItem(key);
  }

  // 消息相关
  getMessageCount() {
    return StorageProvider.MESSAGE_COUNT
  }
  setMessageCount(num) {
    StorageProvider.MESSAGE_COUNT = num
  }

  public static ME_LOGIN_USERINFO="loginuserinfo"
  public static ME_LOGIN_DATE="exDate"  //登陆时间
  public static TOKEN_LIVE_TIME =30 * 6 * 60 * 60 * 1000 //token30天
  public static ME_USERINFO="userinfo"
  public static Me_IMDEFECTIMG='imdefectimg';
  public static Me_IMAGES='images';
  //非wifi状态下  提醒一次为true 每次提醒为false
  public static ISOFFWIFILOAD='isoffwifiload';
  public static LE_LEASSET_SELECTASSET="LE_LEASSET_SELECTASSET";
  public static HOME_APPAUTHORITY="appAuthority"
  public static LOGIN_USERINFO_LIST="loginUserList";
  public static SYSTEM_CONFIG="systemConfig";
  public static ISKEEP_PASSWORD="isKeepPassword";
  public static ALLAPPLICATION="allApplication";
  public static USERINFOACCOUNT='userinfoAccount';
  public static SETTING_FINGER_LOGIN="SETTING_FINGER_LOGIN";
  public static BLUETOOTH_CONNECTED_DEVICE="BLUETOOTH_CONNECTED_DEVICE";
  public static OFFLINE_PI="OFFLINE_PI";
  public static OFFLINE_PI_UPLOAD_INTERVAL="OFFLINE_PI_UPLOAD_INTERVAL";
  //巡检tab
  public static pitaskicons="0";
  //巡检人
  public static PI_TASK_PATROKMANNAME="patrolmanname";

  //精益检修项目id
  public static LM_PROJECTID="lmprojectid";
  // 消息
  public static MESSAGE_COUNT = 0
  // 是否在浏览器容器
  public static IS_BROWSER="isBrowser";
}



