import { Directive, ElementRef, OnInit } from '@angular/core';
declare var $: any 

/**
 * 监听mform组建内input text内容溢出，将input换行
 */
@Directive({
  selector: '[input-overflow]' // Attribute selector
})
export class InputOverflowDirective implements OnInit {
  constructor(public el: ElementRef) {
  }

  ngOnInit() {
    setTimeout(() => {
      const target = this.el.nativeElement.children[0]
      if (target.scrollWidth > target.offsetWidth) {
        // 内容溢出
        this.el.nativeElement.style.flex = 'none'
        this.el.nativeElement.parentElement.style.flexWrap = 'wrap'
        $(this.el.nativeElement).prev()[0].style.marginBottom = 0
      }
    }, 100)
  }
  
}



