  /**
   * 上传附件图片
   * @param busstype 
   * @param bussid 
   * @param imageData 
   */
  uploadify(busstype: string, bussid: String, imageData: any, callback?: any) {
    this.initToken();
    const fileTransfer: FileTransferObject = this.transfer.create();
    var api = '/uploadify/upload?busstype=' + busstype + "&bussid=" + bussid;
    api = Utils.dealUrl(api);
    let filename = Utils.dateFormat(new Date(), 'yyyy-MM-dd hh_mm_ss');
    let options: FileUploadOptions = {
      fileKey: 'file',
      fileName: filename + '.jpg',
      mimeType: 'image/jpeg',
      httpMethod: "POST",
      headers: { 'JwtToken': this.JwtToken }
    }
    fileTransfer.upload(imageData, encodeURI(api), options).then((result) => {
      callback(result);
    });
  }
  uploadifyAwait(busstype: string, bussid: String, imageData: any) {
    this.initToken();
    const fileTransfer: FileTransferObject = this.transfer.create();
    var api = '/uploadify/upload?busstype=' + busstype + "&bussid=" + bussid;
    api = Utils.dealUrl(api);
    let filename = Utils.dateFormat(new Date(), 'yyyy-MM-dd hh_mm_ss');
    let options: FileUploadOptions = {
      fileKey: 'file',
      fileName: filename + '.jpg',
      mimeType: 'image/jpeg',
      httpMethod: "POST",
      headers: { 'JwtToken': this.JwtToken }
    }
    return fileTransfer.upload(imageData, encodeURI(api), options)
  }
  /**
   * 上传视频文件
   * @param busstype 
   * @param bussid 
   * @param imageData 
   * @param callback 
   */
  uploadvideo(busstype: string, bussid: String, imageData: any, callback?: any) {
    this.initToken();
    const fileTransfer: FileTransferObject = this.transfer.create();
    var api = '/uploadify/upload?busstype=' + busstype + "&bussid=" + bussid;
    api = Utils.dealUrl(api);
    let filename = Utils.dateFormat(new Date(), 'yyyy-MM-ddhh:mm:ss');
    filename =  filename.replace(/:/g,"-")
    let options: FileUploadOptions = {
      fileKey: 'file',
      fileName: filename + '.mp4',
      mimeType: 'video/mp4',
      headers: { 'JwtToken': this.JwtToken }
    }
    fileTransfer.upload(imageData, encodeURI(api), options).then((res) => {
      callback(res);
    }).then((err) => {
      console.log(err);
    })
  }
  /**
   * 上传音频文件
   * @param busstype 
   * @param bussid 
   * @param callback 
   */
  uploadmedia(busstype: string, bussid: String, imageData: any, filename: any, callback?: any) {
    this.initToken();
    const fileTransfer: FileTransferObject = this.transfer.create();
    var api = '/uploadify/upload?busstype=' + busstype + "&bussid=" + bussid;
    api = Utils.dealUrl(api);
    if(filename == null){
      filename = Utils.dateFormat(new Date(), 'yyyy-MM-ddhh:mm:ss');
    }

    // let filename = Utils.dateFormat(new Date(), 'yyyy-MM-dd hh:mm:ss');
    let options: FileUploadOptions = {
      fileKey: 'file',
      fileName: filename,
      mimeType: 'audio/wav',
      headers: { 'JwtToken': this.JwtToken }
    }
    fileTransfer.upload(imageData, encodeURI(api), options).then((res) => {
      callback(res);
    }).then((err) => {
      console.log(err);
    })
  }
