declare var $:any 
import { Injectable } from '@angular/core';
import{ HttpServicesProvider} from './http-services';
import 'rxjs/add/operator/toPromise';
/*
  Generated class for the WorkflowServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class WorkflowServiceProvider {

  constructor(public httpService:HttpServicesProvider) {
    console.log('Hello WorkflowServiceProvider Provider');
  }
  checkAuth(wfid,callback?: any){
    this.httpService.get("/workflow/checkAuth/"+wfid,null,(result)=>{
      callback(result);
    });
  }
  /*提交流程*/
  workflowStart(busstype,bussid,callback){
    this.httpService.get("/workflow/start/"+busstype+"/"+busstype+"?code="+busstype,null,callback);
  }
  /*流程审批选项*/
  workflowWay(wfid: string,callback?: any){
    var url="/workflow/way/"+wfid;
    this.httpService.get(url,null,(result)=>{
      callback(result);
    });
  }
  /*发送流程*/
  startAndSenf(busstype,bussid,callback?: any){
    this.httpService.post("/workflow/startAndSenf/"+busstype+"/"+busstype,null,(result)=>{
      callback(result);
    });
  }
  /*流程历史*/
  historylist(wfid: string,callback?: any){
    var url="/workflow/historylist/"+wfid+"?sort=id&order=desc";
    this.httpService.get(url,null,(result)=>{
      callback(result);
    });
  }
  // 检查流程
  workflowcheckAuth(forminfo,page?:any) {
    // console.log(forminfo)
    if (forminfo['wfid'] != "" && forminfo['wfid'] != null) {
      this.checkAuth(forminfo['wfid'], data => {
        if (data.code == 1) {
          if(page != null){
            $(page + ' #saveBtn').hide();
            $(page + ' #workflowStart').hide();
            $(page + ' #ionFooter').hide();
            $(page +  ' .scroll-content').css({'margin-bottom':'0'})
          }else{
            $('#saveBtn').hide();
            $('#workflowStart').hide();
            $('#ionFooter').hide();
            $('.scroll-content').css({'margin-bottom':'0'})
          }
          
  
        }
      })
      if (forminfo['wfstatus'] == "CLOSE") {
        if(page != null){
          $(page + ' #ionFooter').hide();
          $(page + ' .scroll-content').css({'margin-bottom':'0'})
          $(page + ' #saveBtn').hide();
        }else{
          $(' #ionFooter').hide();
          $('.scroll-content').css({'margin-bottom':'0'})
          $('#saveBtn').hide();
        }
      }
    }
  }

}



